"use client";
import { usePathname, useSearchParams } from "next/navigation";
import { useRouter } from "next/router";

function Filter() {
  const searchParams = useSearchParams();
  const pathname = usePathname();
  const router = useRouter();

  function handelFilter(filter) {
    const params = new URLSearchParams(searchParams);
    params.set("capacity", filter);
    router.replace(`${pathname}?${params}`);
  }
  return (
    <div className="border border-primary-800">
      <Button onClick={() => handelFilter("all")}>all guests</Button>
      <Button onClick={() => handelFilter("small")}>1-3 guests</Button>
      <Button onClick={() => handelFilter("medium")}>4-7 guests</Button>
      <Button onClick={() => handelFilter("large")}>8-12 guests</Button>
    </div>
  );
}

function Button({ children, onClick }) {
  return (
    <button
      onClick={onClick}
      className="capitalize px-5 py-3 transition-all duration-150 hover:bg-primary-900"
    >
      {children}
    </button>
  );
}

export default Filter;
